h1 {
    font-size: 3rem;
}

h4 {
    margin-top: 2rem;
    color: #d9d9d9;
    font-size: 1.3rem;
}

.hero-section {
    height: 100vh;
    background-image: url('../../assets/desert.jpg');
    background-position: center;
    /* background-repeat: no-repeat; */
    background-size: cover;
}

.background-cover {
    width: 100%;
    height: 100vh;
    background-color: #0007;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero-content {
    text-align: center;
    color: white;
}

.arrow-down {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    bottom: 20px;
}