h2 {
    font-size: 2rem;
}

.projects-section {
    text-align: center;
    padding: 0 15%;
    padding-top: 2rem;
    padding-bottom: 4rem;
    background-color: #444;
    color: white;
}

.section-title {
    
}

.projects-grid {
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-gap: 20px;
    padding: 3rem 2rem;
}

.grid-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* font-size: 2rem; */
    height: 200px;
    background-color: #ddd;
    color: black;
}

.project-image {
    width: 60%;
    height: 70%;
    margin-bottom: 10px;
}