li {
    display: inline;
    padding: 2px 20px;
    font-size: 1.2rem;
}

a {
    text-decoration: none;
    color: #ddd;
}

a:hover {
    color: white;
}

.header-box {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #000;
    color: #fff;
    padding: 10px 4rem;
    z-index: 1;
}

.logo {
    display: flex;
    align-items: center;
    font-size: 1.3rem;
    font-weight: bold;
}

