.resume-section {
    text-align: center;
    padding-top: 2rem;
    padding-bottom: 4rem;
    background-color: #444;
    color: white;
}

.resume-blocks {
    display: flex;
    flex-direction: column;
    padding-top: 1rem;
}

.resume-block {
    position: relative;
}

.resume-img {
    
}

.resume-btn {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: -90px;
    font-size: 1.5rem;
    color: white;
    padding: 0.5rem 3rem;
    background-color: #000c;
    border: none;
    border-radius: 3rem;
    transition: background-color 0.5s;
}

.resume-btn:hover {
    cursor: pointer;
    background-color: #000f;
    color: white;
}