.contact-section {
    padding: 0 15%;
    padding-top: 2rem;
    padding-bottom: 4rem;
    text-align: center;
}

.contact-section a {
    color: #666;
}

.contact-blocks {
    display: flex;
}

.contact-block {
    display: grid;
    grid-template-columns: 2.2rem auto;
    grid-row-gap: 1rem;
    grid-column-gap: 1rem;
    text-align: left;
}

.contact-grid-item {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
}